import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import InputAdornment from "@mui/material/InputAdornment";

const dispatchedEvents = [];

const SrsMuiInputAdornment = function (props, ref) {
  return (
    <InputAdornment {...props} theme={props.muiTheme}>
      <slot />
    </InputAdornment>
  );
};

const styles = `
  /* Custom styles for the MuiInputAdornment component */
`;

const props = [
  {
    name: "disablePointerEvents",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "disableTypography",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
  // Add other props specific to InputAdornment if needed
];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiInputAdornment),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
